:root{font-size:16px;--text-primary: #b6b6b6;--text-secondary: #ececec;--bg-primary: #23232e;--bg-secondary: #141418;--transition-speed: 900ms}.navbar{position:fixed;background-color:#23232e;background-color:var(--bg-primary);transition:width 200ms ease}.navbar-nav{list-style:none;padding:0;margin:0;display:flex;flex-direction:column;align-items:center}.nav-link{display:flex;align-items:center;height:5rem;color:#b6b6b6;color:var(--text-primary);text-decoration:none;-webkit-filter:grayscale(100%) opacity(0.7);filter:grayscale(100%) opacity(0.7);transition:900ms;transition:var(--transition-speed)}.nav-link .icon{font-size:30px;color:tomato}.nav-link:hover{-webkit-filter:grayscale(0%) opacity(1);filter:grayscale(0%) opacity(1);color:#ececec;color:var(--text-secondary)}.link-text{display:none;margin-left:1rem}.nav-link i{min-width:2rem;margin:0 1.5rem}.navbar:hover{width:18rem}.navbar:hover .link-text{display:block}.logo{font-weight:bold;text-transform:uppercase;margin-bottom:1rem;text-align:center;color:#ececec;color:var(--text-secondary);background:#141418;background:var(--bg-secondary);font-size:1.5rem;letter-spacing:0.3ch;width:100%}.logo .icon{transform:rotate(0deg);transition:transform 900ms;transition:transform var(--transition-speed)}.navbar:hover .logo .icon{transform:rotate(-180deg)}@media only screen and (max-width: 600px){.navbar{bottom:0;width:100vw;height:5rem}.logo{display:none}.navbar-nav{flex-direction:row}.nav-link{justify-content:center}main{margin:0}}@media only screen and (min-width: 600px){.navbar{top:0;width:5rem;height:100vh}.navbar:hover{width:16rem}.navbar:hover .link-text{display:inline;transition:opacity 900ms;transition:opacity var(--transition-speed)}}main{transition:background-color 0.4s ease-in;margin-left:0}.toggle-container{display:flex;justify-content:center;padding:30px}#toggle{width:50px;padding:5px;background-color:#1a202c;border-radius:5px;cursor:pointer;box-shadow:0px 5px 20px -10px #1e1e24;transition:background-color 0.2s ease-in